<template>
  <!-- 员工选择 -->
  <el-dialog title="员工选择" v-model="visible" top="30px" size="tiny">
    <input type="text" :value="value" style='display:none;'>
    <el-input
      placeholder="输入姓名进行查询"
      icon="search"
      v-model="filterText">
    </el-input>
    <el-tree
      :data="allUser"
      :props="props"
      show-checkbox
      node-key="id"
      highlight-current
      class="scrollable m-t-sm"
      style="max-height: 300px;"
      ref="allUser"
      :filter-node-method="filterNode"
      v-loading="choosePersonLoading"></el-tree>
    <div slot="footer" class="dialog-footer">
      <el-button size="mini" @click="selectAll" type="success" class="pull-left" :disabled="!!filterText">全选
      </el-button>
      <el-button size="mini" @click="cancelAll" type="danger" class="pull-left">清空</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </div>
  </el-dialog>

</template>

<script>
  import {getterApi} from '@/config'
  import propsync from '../mixins/propsync'
  export default{
    name: 'choose-user',
    mixins: [propsync],
    data () {
      return {
        // 查询字
        filterText: '',
        // 所有用户
        allUser: [],
        // 配置选项
        props: {
          label: 'name'
        },
        // 加载状态
        choosePersonLoading: false,
        visible: false,
      }
    },
    props: {
      // 显示隐藏
      value: {
        type: Boolean,
        default: false,
        propsync: false
      },
      // 目标文本框
      targetName: {
        type: String,
        required: true
      },
      // 目标文本框
      targetUid: {
        type: Array,
        required: true
      }
    },
    components: {},
    watch: {
      // 监视查询输入
      filterText(val) {
        this.$refs.allUser.filter(val);
      },
      value(val) {
        this.visible = val;
      },
      visible(val) {
        this.$emit('input', val);
      }
    },
    methods: {
      // 获取全部用户
      getAllUser(){
        this.choosePersonLoading = true;
        getterApi.getAllUser.call(this, data => {
          this.allUser = data.userList;
          this.choosePersonLoading = false;
        })
      },
      
      // 过滤器
      filterNode(value, data) {
        if (!value) return true;
        return data.name.indexOf(value) !== -1;
      },
      
      // 全选
      selectAll(){
        let list = this.allUser.map(item => item.id);
        this.$refs.allUser.setCheckedKeys(list)
      },
      
      // 取消全选
      cancelAll(){
        this.$refs.allUser.setCheckedKeys([])
      },
      
      // 选择用户
      submit() {
        let selectList = this.$refs.allUser.getCheckedNodes();
        this.p_targetName = selectList.length === this.allUser.length ? '所有人' : selectList.map(item => item.name).toString();
        this.p_targetUid = selectList.length === this.allUser.length ? ['000'] : selectList.map(item => item.id);
        this.visible = false;
      },
    },
    mounted () {
      this.getAllUser();
      if (this.value) {
        this.visible = true;
      }
    }
  }
</script>



