/* 不属于任何模块API */
import ajax from '../request'
export default {
  /**
   * 登录
   * @param {object}    data          参数
   * @param {string}    data.username 登陆用户名
   * @param {string}    data.password 登陆密码
   * @param {function}  fn            成功回调
   * @param {Function}  errFn         失败回调
   */
  login(data, fn, errFn) {
    ajax.call(this, 'post', '/login', data, fn, true, errFn);
  },
  
  /**
   * 修改密码
   * @param  {object}   data 参数
   * @param {string} data.old_password 旧密码
   * @param {string} data.password 新密码
   * @param {string} data.password_confirm 确认密码
   * @param  {Function} fn   成功回调
   */
  upPwd(data, fn) {
    ajax.call(this, 'post', '/common/upPwd', data, fn);
  },
  /**
   * 获取头部消息列表
   * @param  {Function} fn 成功回调
   */
  getUnreadNotice(fn) {
    ajax.call(this, 'get', 'unreadNotice.json', {}, fn);
  },
  /**
   * 获取左边导航
   * @param  {Function} fn 成功回调
   */
  getLeftNav(fn) {
    ajax.call(this, 'get', 'leftNav.json', {}, fn);
  },
}



