<template>
  <section class="vbox" @click="hide_popup">
    <!-- 头部导航 -->
    <head-nav></head-nav>
    <!-- 主体内容 -->
    <section class="hbox stretch">
      <!-- 左侧菜单 -->
      <aside-nav></aside-nav>
      <!-- 中心内容 -->
      <section>
        <div class="clearfix navbar-fixed-top-xs bg-white b-b" style="z-index: 1000;">
          
          <el-breadcrumb class="m-sm">
            <!-- 后退 -->
            <span class="pull-left m-r-md visible-xs"><i class="fa fa-chevron-left" @click="$router.go(-1)"></i></span>
            <!-- 前进 -->
            <span class="pull-right visible-xs"><i class="fa fa-chevron-right" @click="$router.go(1)"></i></span>
            <el-breadcrumb-item v-for="item in breadcrumb" :key="item.name">
              <i class="fa" :class="item.meta.icon"></i>
              {{item.name}}
            </el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <section class="vbox">
          <section class="scrollable padder">
            <div class="m-b-md" >
              <transition name="custom-classes-transition"
                            enter-active-class="animated fadeInLeft"
                          leave-active-class="animated fadeOutLeft"
                          mode="out-in">
                <h3 class="m-t-sm" v-if="$route.name != '主页'">{{$route.name}}</h3>
              </transition>
            </div>
            <!-- 主视图 -->
            <transition name="custom-classes-transition"
                        enter-active-class="animated fadeInDown"
                        leave-active-class="animated fadeOutDown"
                        mode="out-in">
              <router-view></router-view>
            </transition>
          </section>
        </section>
      </section>
      <!-- 右侧隐藏消息列表 -->
      <aside class="bg-light lter b-l aside-md hide animated"
             :class="{'show fadeInRight':this.$store.state.nav.show_all_notification}">
        <div class="wrapper">通知</div>
      </aside>
    </section>
  </section>
</template>

<script>
  import {nav} from '@/components'
  export default{
    name: 'index',
    data () {
      return {}
    },
    components: {
      'head-nav': nav.HeadNav,
      'aside-nav': nav.AsideNav,
    },
    watch: {},
    computed: {
      breadcrumb () {
        let list = [];
        this.$route.matched.forEach(item => {
          item.name && list.push(item);
        });
        return list;
      }
    },
    methods: {
      /**
       * 隐藏小弹窗
       * */
      hide_popup(){
        this.$store.dispatch('hide_popup');
      },
    },
    created () {
      this.$store.dispatch('update_sys');
      document.querySelector('html').className = 'app';
    }
  }
</script>

<style scoped>
  section.stretch.hbox > section{
    transition: transform 0.2s ease-in-out;
  }
</style>



