// 全局设置
const gbs = {
    // 接口根地址。本地代理可在/config/index.js -> proxyTable中配置
    /*
     *  proxyTable: {
     *    '/static/data/': {
     *      target: 'http://xxxxxx.com',
     *      changeOrigin: true,
     *      pathRewrite: {
     *        '^/static/data/': ''
     *      }
     *    }
     *  },
     * */
    //host: 'http://192.168.17.50:808',
    host: '/static/data/',
    // 本地存储的key
    db_prefix: 'sx_admin_',
  },

  // 回调设置
  cbs = {
    /**
     * ajax请求成功，返回的状态码不是200时调用
     * @param  {object} err 返回的对象，包含错误码和错误信息
     */
    statusError(err) {
      if (err.status !== 404) {
        this.$message({
          showClose: true,
          message: '返回错误：' + err.message,
          type: 'error'
        });
      } else {
        this.$store.dispatch('remove_userinfo').then(() => {
          this.$alert(err.status + ',' + err.message + '！', '登录错误', {
            confirmButtonText: '确定',
            callback: action => {
              this.$router.push('/login');
            }
          });
        });
      }
    },

    /**
     * ajax请求网络出错时调用
     */
    requestError(err, url) {
      this.$message({
        showClose: true,
        message: '请求错误(' + err.response.status + ')：' + url + ',' + err.response.statusText,
        type: 'error'
      });
    }
  };

export {gbs, cbs}



