<style lang="less" scoped>
  .androidDown, .iosDown {
    position: relative;
    img {
      position: absolute;
      top: 50px;
      left: -55px;
      width: 150px;
      z-index: 5;
    }
    &:hover {
      img {
        display: block !important;
      }
    }
  }
</style>
<template>
  <!-- 头部 -->
  <header class="bg-dark dk header navbar navbar-fixed-top-xs">
    <!-- 左侧logo及移动端菜单和用户信息控制按钮 -->
    <div class="navbar-header aside-md">
      <a class="btn btn-link visible-xs" @click.stop="toggle_show_leftnav"
         :class="{'active':this.$store.state.nav.show_leftNav}">
        <i class="fa fa-bars"></i>
      </a>
      <a href='javascript:' class="navbar-brand" @click="screenfull">
        <img src="../../assets/images/logo.png" class="m-r-sm">商翔集团
      </a>
      <a class="btn btn-link visible-xs" @click.stop="toggle_show_user"
         :class="{'active':this.$store.state.nav.show_user}">
        <i class="fa fa-cog"></i>
      </a>
    </div>
    <!-- 右侧 -->
    <ul class="nav navbar-nav navbar-right m-n  hidden-xs nav-user"
        :class="{'open':this.$store.state.nav.show_user}">
      <!-- 安卓下载 -->
      <li class="hidden-xs androidDown">
        <a href="javascript:">
          <i class="fa fa-android"></i>
        </a>
        <img src="../../assets/images/androidlogo.png" alt="安卓下载" class="hide animated fadeInDown">
      </li>
      
      <!-- 苹果下载 -->
      <li class="hidden-xs iosDown">
        <a href="javascript:">
          <i class="fa fa-apple"></i>
        </a>
        <img src="../../assets/images/IOSlogo.jpg" alt="苹果下载" class="hide animated fadeInDown">
      </li>
      
      <!-- 通知信息 -->
      <li class="hidden-xs" :class="{'open':this.$store.state.nav.show_notification}">
        <a href="javascript:" class="dk" @click.stop="toggle_show_notification">
          <i class="fa fa-bell"></i>
          <span class="badge badge-sm up bg-danger m-l-n-sm"
                v-show="unreadNotice.length">{{unreadNotice.length}}</span>
        </a>
        <section class="dropdown-menu aside-xl">
          <section class="panel bg-white">
            <header class="panel-heading b-light bg-light">
              <strong v-if="unreadNotice.length">你有 <span class="count">{{unreadNotice.length}}</span> 条通知</strong>
              <strong v-else>暂无通知</strong>
            </header>
            <div class="list-group list-group-alt animated fadeInRight">
              <notification-item v-for="item in unreadNotice" :item="item" :key="item.id"></notification-item>
            </div>
            <footer class="panel-footer text-sm">
              <a href="javascript:" @click="toggle_show_all_notification"
                 :class="{'active':this.$store.state.nav.show_all_notification}">查看全部通知</a>
            </footer>
          </section>
        </section>
      </li>
      
      <!-- 账户信息 -->
      <li :class="{'open':this.$store.state.nav.show_usersetting}" class="dropdown">
        <a href="javascript:" @click="toggle_show_usersetting">
          <span class="thumb-sm avatar pull-left">
              <img :src="this.$store.state.user.userinfo.avatar">
          </span> {{this.$store.state.user.userinfo.username}}
          <b class="caret"></b>
        </a>
        <ul class="dropdown-menu animated fadeInRight">
          <span class="arrow top"></span>
          <li><a href="javascript:" @click="upPwdDialog = true;">修改密码</a></li>
          <!--<li>
            <a href="#">
              <span class="badge bg-danger pull-right">{{unreadNotice.length}}</span> 通知
            </a>
          </li>-->
          <li class="divider"></li>
          <li>
            <router-link to="/Documentation">CSS文档</router-link>
          </li>
          <li>
            <router-link to="/API">API文档</router-link>
          </li>
          <li class="visible-xs"><a href="javascript:" @click="logout">注销</a></li>
        </ul>
      </li>
      
      <!-- 退出 -->
      <li class="hidden-xs">
        <a href="javascript:" @click="logout" class="dropdown-toggle dker fa fa-sign-out"></a>
      </li>
    </ul>
    
    <!-- 修改密码 -->
    <el-dialog title="修改密码" v-model="upPwdDialog" size="tiny" @close="$refs.upPwdForm.resetFields()">
      <el-form :model="upPwdForm" :rules="upPwdRules" label-width="85px" ref="upPwdForm">
        <el-form-item label="原密码" prop="oldPwd">
          <el-input v-model="upPwdForm.oldPwd" auto-complete="off" type="password" @keyup.native.enter="submitUpPwd('upPwdForm')"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="newPwd">
          <el-input v-model="upPwdForm.newPwd" auto-complete="off" type="password" @keyup.native.enter="submitUpPwd('upPwdForm')"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="conPwd">
          <el-input v-model="upPwdForm.conPwd" auto-complete="off" type="password" @keyup.native.enter="submitUpPwd('upPwdForm')"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="$refs.upPwdForm.resetFields()">重置</el-button>
        <el-button type="primary" @click="submitUpPwd('upPwdForm')">确定</el-button>
      </div>
    </el-dialog>
  </header>

</template>

<script>
  import screenfull from 'screenfull'
  import listItem from '../listItem'
  import {commonApi} from '@/config'
  export default{
    name: 'header-nav',
    data () {
      let oldPwd = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('密码不能为空'));
        }
        else if (value.length < 6 || value.length > 16) {
          return callback(new Error('长度在 6 到 16 个字符'))
        } else {
          callback()
        }
      };
      let newPwd = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入新密码'));
        } else if (value.length < 6 || value.length > 16) {
          return callback(new Error('长度在 6 到 16 个字符'))
        } else if (this.upPwdForm.conPwd !== '') {
          this.$refs.upPwdForm.validateField('conPwd');
          callback();
        }
      };
      let conPwd = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value.length < 6 || value.length > 16) {
          return callback(new Error('长度在 6 到 16 个字符'))
        } else if (value !== this.upPwdForm.newPwd) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        unreadNotice: [],
        upPwdDialog: false,
        upPwdForm: {
          oldPwd: '',
          newPwd: '',
          conPwd: ''
        },
        upPwdRules: {
          oldPwd: [
            {validator: oldPwd, trigger: 'blur'}
          ],
          newPwd: [
            {validator: newPwd, trigger: 'blur'}
          ],
          conPwd: [
            {validator: conPwd, trigger: 'blur'}
          ],
          
        }
      }
    },
    components: {
      'notification-item': listItem.NotificationItem
    },
    watch: {},
    methods: {
      /**
       * 重置密码
       */
      submitUpPwd(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let para = Object.assign({}, this.upPwdForm);
            commonApi.upPwd.call(this, para, data => {
              this.$message({
                message: '修改成功,请重新登陆',
                type: 'success'
              });
              this.$store.dispatch('remove_userinfo').then(() => {
                this.upPwdDialog = false;
                this.$router.push('/signin');
              });
            })
          }
        })
      },
      /**
       * 退出登录
       */
      logout() {
        this.$confirm('你确定退出登录么?', '确认退出', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$store.dispatch('remove_userinfo').then(() => {
            this.$message({
              type: 'success',
              message: '成功退出登录',
              showClose: true
            });
            this.$router.push('/signin');
          }).catch(e => console.log('移除用户信息出错:' + e));
        }).catch(() => {
          this.$message({
            message: '已取消',
            showClose: true
          });
        });
      },
      /**
       * 显示隐藏侧边导航 (移动端下)
       * */
      toggle_show_leftnav(){
        this.$store.dispatch('toggle_show_leftnav');
      },
      /**
       * 显示隐藏用户信息 (移动端下)
       * */
      toggle_show_user(){
        this.$store.dispatch('toggle_show_user');
      },
      /**
       * 显示隐藏通知信息
       * */
      toggle_show_notification(){
        this.$store.dispatch('toggle_show_notification');
      },
      /**
       * 显示隐藏全部通知信息
       * */
      toggle_show_all_notification(){
        this.$store.dispatch('toggle_show_all_notification');
      },
      /**
       * 显示隐藏用户设置
       * */
      toggle_show_usersetting(e){
        this.$store.state.user.sys == 'pc' && e.stopPropagation();
        this.$store.dispatch('toggle_show_usersetting');
      },
      /**
       * 全屏/取消全屏
       * */
      screenfull(){
        if (screenfull.enabled) {
          screenfull.toggle();
        }
      },
    },
    beforeMount () {
      /*commonApi.getUnreadNotice.call(this, data => {
       this.unreadNotice = data
       });*/
    },
  }
</script>



